.head{position:relative;width:100%;height:64px;box-shadow:0 1px 4px rgba(0,21,41,.08);transition:background .3s,width .2s}.head :global .ant-menu-submenu.ant-menu-submenu-horizontal{height:100%;line-height:64px}.head :global .ant-menu-submenu.ant-menu-submenu-horizontal .ant-menu-submenu-title{height:100%}.head.light{background-color:#fff}.head .main{display:flex;height:64px;padding-left:24px}.head .main.wide{max-width:1200px;margin:auto;padding-left:0}.head .main .left{display:flex;flex:1 1}.head .main .right{width:324px}.logo{position:relative;width:165px;height:64px;overflow:hidden;line-height:64px;transition:all .3s}.logo img{display:inline-block;height:32px;vertical-align:middle}.logo h1{display:inline-block;margin:0 0 0 12px;color:#fff;font-weight:400;font-size:16px;vertical-align:top}.light h1{color:#002140}.menu{height:64px;line-height:64px;border:none}